import React from 'react';
import styles from './About.module.css';

import AboutContents from './AboutContents/AboutContents';
import Profile from './AboutContents/Profile';

const About = () => {
    return (
        <section>
            <div className={styles.row}>
                <h2 data-aos="fade-right">About</h2>
                <div data-aos="fade-left" className={styles.headerBar}>&mdash;</div>
                    <AboutContents />   
                    <Profile />                  
            </div> 
        </section>
    );
}

export default About;