import React, {Component} from 'react';
import { Link, Element, Events, animateScroll as scroll, scroller } from 'react-scroll';
import ReactRotatingText from 'react-rotating-text';
import ParticlesJS from '../Components/Landing/ParticlesJS';
import ContactLinks from '../Components/Landing/ContactLinks';
import About from '../Components/About/About';
import Projects from '../Components/Projects/Project';
import Blogs from '../Components/Blogs/Blogs';
import Contact from './Contact/Contact';
import styles from '../Components/Landing/Landing.module.css';
import navStyles from './Nav.module.css';
import './App.module.css';

class App extends Component {
    constructor(props) {
        super(props);
        this.scrollToTop = this.scrollToTop.bind(this);
    }
    state = {isTop: true}  
    
    componentDidMount() {
        Events.scrollEvent.register('begin', function () {
            console.log("begin", arguments);
        });
        Events.scrollEvent.register('end', function () {
            console.log("end", arguments);
        });
        document.addEventListener('scroll', () => {
            const isTop = window.scrollY < 860;
            if (isTop !== this.state.isTop) {
                this.setState({ isTop })
            }
          });
    }
    scrollToTop() {
        scroll.scrollToTop();
    }
    scrollToBottom() {
        scroll.scrollToBottom();
    }
    scrollTo() {
        scroller.scrollTo('scroll-to-element', {
            duration: 800,
            delay: 0,
            smooth: 'easeInOutQuart'
        })
    }
    scrollToWithContainer() {
        let goToContainer = new Promise((resolve, reject) => {    
            Events.scrollEvent.register('end', () => {
                resolve();
                Events.scrollEvent.remove('end');
            });

            scroller.scrollTo('scroll-container', {
            duration: 800,
            delay: 0,
            smooth: 'easeInOutQuart'
            });
        });
    
        goToContainer.then(() =>
            scroller.scrollTo('scroll-container-second-element', {
                duration: 800,
                delay: 0,
                smooth: 'easeInOutQuart',
                containerId: 'scroll-container'
          }));
      }     
    componentWillUnmount() {
        Events.scrollEvent.remove('begin');
        Events.scrollEvent.remove('end');
    }
    render() { 
        return (
            <div>   
                <header>
                    <div style={this.state.isTop ? {display:"none"} : {display:"block"}} className={navStyles.menuWrap}>
                        <a href="#" className={navStyles.navBrand}><span>CESSY</span><small>Web Developer</small></a>
                            <nav>
                                <ul className={navStyles.mainNav}>
                                    <li><a href="#" onClick={this.scrollToTop}>Home</a></li>   
                                    <li><Link activeClass="active" to="about" spy={true} smooth={true} duration={500}>About</Link></li>                                          
                                    <li><Link activeClass="active" to ="projects" spy={true} smooth={true} duration={500}>Projects</Link></li>
                                    <li><Link activeClass="active" to ="blogs" spy={true} smooth={true} duration={500}>Blogs</Link></li>
                                    <li><a href="#" onClick={this.scrollToBottom}>Contact</a></li>
                                </ul> 
                            </nav>                           
                            <input type="checkbox" className={navStyles.toggler} />
                            <div className={navStyles.hamburger}><div></div></div>
                            <div className={navStyles.menu}>
                                <div>
                                    <div>                           
                                    <ul className={navStyles.mobile}>
                                        <li><a href="#" onClick={this.scrollToTop}>Home</a></li>   
                                        <li><Link activeClass="active" to="about" spy={true} smooth={true} duration={500}>About</Link></li>                                          
                                        <li><Link activeClass="active" to ="projects" spy={true} smooth={true} duration={500}>Projects</Link></li>
                                        <li><Link activeClass="active" to ="blogs" spy={true} smooth={true} duration={500}>Blogs</Link></li>
                                        <li><a href="#" onClick={this.scrollToBottom}>Contact</a></li>
                                    </ul> 
                                    </div>
                                </div>
                            </div>
                        </div>
                </header>
                
                <Element name="home">
                    <div className={styles.BG}>            
                        <div className={styles.introBox}>
                            <ParticlesJS />
                            <div className={styles.introBoxText}>
                                <h1>Cessy</h1>
                                <h3><ReactRotatingText items={['WEB DEVELOPER ', 'CREATIVE ', 'INNOVATIVE ','DETAIL-ORIENTED ']}/></h3>
                                <ContactLinks />
                                <div className={styles.viewWork}>
                                    <Link activeClass="active" to="about" spy={true} smooth={true} duration={500}>View My Work</Link>
                                </div>
                            </div>                   
                        </div>                   
                    </div>
                </Element>    
                <Element name="about">
                    <About />
                </Element>
                <Element name="projects">
                    <Projects />
                </Element>
                <Element name="blogs">
                    <Blogs />
                </Element>
                <Element name="contact">
                    <Contact />
                </Element>
            </div>                              
        )
    }
}
export default App;