import React from 'react';
import {learnCode, planning} from '../../assets/images';
import Card from './Card/Card';
import styles from './Blogs.module.css';
import Article1 from './Articles/Article1';
import Article2 from './Articles/Article2';

const Blogs = () => {
    return(
        <div>
            <section className={styles.BG}>
                <div className={styles.row}>
                    <h2 data-aos="fade-right">Blog</h2>
                    <div data-aos="fade-left" className={styles.headerBar}>&mdash;</div>
                    <div className={styles.blogs}>
                        <Card 
                            image={learnCode} 
                            date={'July 19, 2020'}
                            title={'Learning Web Development in the Middle of A Pandemic'}
                            desc={'Tips to effective learning in the middle of a chaotic environment.'}    
                            blog={<Article1 />}                                        
                        />
                        <Card 
                            image={planning}
                            date={'July 19, 2020'}
                            title={'Midlife Career Change'}
                            desc={'Becoming a Web Developer in your 40s.'} 
                            blog={<Article2 />}                                            
                        />
                    </div>
                </div>
            </section>           
        </div>
    );
}

export default Blogs;