import React from 'react';
import styles from './Footer.module.css';
import Tooltip from 'react-tooltip';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

const Footer = () => {
    return(
        <footer>
           <div className={styles.row}>
               <div>
                    <a href="https://www.linkedin.com/in/cecilia-nucaza-sy/"><div className={styles.i} data-tip="linked-in"><FontAwesomeIcon icon={['fab', 'linkedin-in']}/></div></a>
                    <a href="https://github.com/cessycode"><div className={styles.i} data-tip="github"><FontAwesomeIcon icon={['fab', 'github']}/></div></a>
                    <a href="https://codepen.io/cessycode"><div className={styles.i} data-tip="codepen"><FontAwesomeIcon icon={['fab', 'codepen']}/></div></a>
                    <a href="https://drive.google.com/file/d/1gX4hXLUstcG7jAvrETQpYa_x3s2JWVVN/view?usp=sharing"><div className={styles.i} data-tip="download resume"><FontAwesomeIcon icon={['fas', 'file']}/></div></a>
                    <h6>By &nbsp;<span className={styles.copyright}>&copy;Cecilia Nucaza-Sy</span>&nbsp; 2020</h6>
                </div>
                <Tooltip place="bottom"/>
           </div>
       </footer>          
        
    );
}

export default Footer;