import React from 'react';
import Card from './Card/Card';
import styles from './Project.module.css';

import {
    music1, face1, eats1, photo1, recipe1, robot1,
    music2, face2, eats2, photo2, recipe2, robot2,
    music3, face3, eats3, photo3, recipe3, robot3
    
} from '../../assets/images';

const Project = () => {
    const eats=[eats1, eats2, eats3],
          robots=[robot1, robot2, robot3],
          recipes=[recipe1, recipe2, recipe3],
          music=[music1, music2, music3],
          face=[face1, face2, face3],
          photo=[photo1, photo2, photo3];
    
    const title=["HealthyEats", "FindRoboApp", "RecipeApp", "CessyMusic", "FaceRecognitionApp", "PhotographySite"],
          desc=["HTML5 CSS3 Javascript", "HTML5 CSS3 Javascript React", "HTML5 CSS3 Bootstrap"],
          desc2=["Restaurant", "Search Robot Engine", "Recipe Search Engine", "Artist Page", "Identifies Human Face In A Given Digital Image", "Photographer's Portfolio"],
          ps=["Restaurant website that offers healthy options. CSS-Grid Responsive Design",
              "A simple website made with React. It demonstrates basic search and retrieve functions using data from an online server.",
              "Made with MVC design. Retrieves data from an online server.",
              "An Artist Music Page. Responsive Web Design",
              "Demonstrates authentication system. Signing-up, Logging-in, Redirection, and Authentication Tokens. Ensures only signed-up clients can view private page. Limit: Detects 1 face per photo.",
              "A website that showcases a photographer's work. Responsive Web Design with Bootstrap"
             ],
          link=["https://cessycode.github.io/HealthyEats/", "https://cessycode.github.io/roboFindApp/", "https://brave-booth-54a11a.netlify.app/", "https://cessycode.github.io/cessymusic/", "https://facescanner-app.herokuapp.com/", "https://cessycode.github.io/PureGems/"];

    return(
        <div>
            <section className={styles.BG}>
                <div className={styles.row}>
                    <h2 data-aos="fade-right">Projects</h2>
                    <div data-aos="fade-left" className={styles.headerBar}>&mdash;</div>  
    
                    <div className={styles.showcase}>                     
                        <Card image={eats} delay={"800"} title={title[0]} desc={desc[0]} desc2={desc2[0]} ps={ps[0]} link={link[0]}/>
                        <Card image={robots} delay={"1000"} title={title[1]} desc={desc[1]} desc2={desc2[1]} ps={ps[1]} link={link[1]}/>
                        <Card image={recipes} delay={"1200"} title={title[2]} desc={desc[0]} desc2={desc2[2]} ps={ps[2]} link={link[2]}/>
                        <Card image={music} delay={"1400"} title={title[3]} desc={desc[0]} desc2={desc2[3]} ps={ps[3]} link={link[3]}/>
                        <Card image={face} delay={"1600"} title={title[4]} desc={desc[1]} desc2={desc2[4]} ps={ps[4]} link={link[4]}/>
                        <Card image={photo} delay={"1800"} title={title[5]} desc={desc[2]} desc2={desc2[5]} ps={ps[5]} link={link[5]}/>                    
                    </div>                   
                </div>
            </section>
        </div>
    );
}

export default Project;