import React from 'react';
import Hexa from './Hexa';
import styles from './AboutContents.module.css';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

const AboutContents = () => {
    return (
        <div className={styles.contents}>
                <Hexa                        
                    icon={<FontAwesomeIcon icon={['fas', 'tachometer-alt']}/>}
                    title={'Fast'}
                    desc = {'Fast load times.'}
                    delay={"800"}
                />               
                <Hexa                               
                    icon={<FontAwesomeIcon icon={['fas', 'laptop-code']}/>}    
                    title={'Responsive'}
                    desc = {'My layouts will work on any device, big or small.'}
                    delay={"1000"}
                />
                <Hexa                               
                    icon={<FontAwesomeIcon icon={['fas', 'lightbulb']}/>}
                    title={'Intuitive'}
                    desc = {'Easy to use and understand.'}
                    delay={"1200"}
                />
                <Hexa                               
                    icon={<FontAwesomeIcon icon={['fas', 'rocket']}/>}
                    title={'Interactive'}
                    desc = {'Expressive animations for more user engagement.'}
                    delay={"1400"}
                />
        </div>
    );
}

export default AboutContents;