import React, {useState} from 'react';
import styles from './Card.module.css';

import {Modal} from 'react-responsive-modal';
import 'react-responsive-modal/styles.css';

const Card = (props) => {
    const {image, date, title, desc, blog} = props;
    const [open, setOpen] = useState(false);
    const closeIcon= <div className={styles.closeModal}></div>;
    return(
        <div className={[styles.col, styles.span1of2].join(' ')} data-aos="zoom-in" data-aos-delay="800">
                <a href="#" onClick={() => setOpen(true)}><img src={image} alt="card-img"/></a>
                <a href="#" onClick={() => setOpen(true)}><h4>{title}</h4></a>
                <h6>{date}</h6>
                <div className={styles.headerBar}>&mdash;</div>  
                <p>{desc}</p>   
                <Modal open={open} onClose={() => setOpen(false)} center  closeIcon={closeIcon}>
                   <div className={styles.modalContent}>
                       {blog}
                   </div>
                   
                </Modal>   
        </div>
    );
}

export default Card;