import React, {useState} from 'react';

import { Modal } from 'react-responsive-modal';
import 'react-responsive-modal/styles.css';

import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css'

import styles from './Card.module.css'; 

const Card = (props) => {
    const {image, delay, title, desc, desc2, ps, link} = props;
    const [open, setOpen] = useState(false);
    const slideImages = [...image];
    const closeIcon = <div className={styles.closeModal}></div>;
    const properties = {
        duration: 5000,
        transitionDuration: 500,
        infinite: true,
        prevArrow: <div className={styles.prev}>&#10094;</div>,
        nextArrow: <div className={styles.next} >&#10095;</div>
    }
    
    return (
        <div className={styles.card} data-aos="fade-up" data-aos-duration="800" data-aos-delay={delay}>
            <img src={slideImages[0]} alt="card-img"/>         
            <div className={styles.cardDesc}>
                <div className={styles.cardTitle}>
                    <h4>{title}</h4>
                    <h5>{desc}</h5>
                </div>
                <button className={styles.cardButton} onClick={() => setOpen(true)}>                   
                    Learn More
                </button> 
                <Modal open={open} onClose={() => setOpen(false)} center  closeIcon={closeIcon}>
                        <div className={styles.modalContent}>
                            <div className={styles.slidesContent}>                                
                                <Slide {...properties}>
                                    {slideImages.map((e, i) => (
                                        <div key={i} >                                                   
                                            <img src={e} alt="card-img"/>                                                    
                                        </div>
                                    ))}
                                </Slide>
                            </div>                                                                               
                            <hr/>
                            <div className={styles.slidesContentDesc}>
                                <h4>{title}</h4>
                                <h5>{desc2}</h5>
                                <p>{ps}</p>                                            
                                <a href={link} target="_blank"><button>View Site</button></a>                                          
                            </div>                                             
                        </div>   
                </Modal>              
            </div>
        </div>
    );
}

export default Card;