import React from 'react';
import styles from './Hexa.module.css';

const Hexa = (props) => {
    const {icon, title, desc, delay} = props;
    return(
        <div>
            <div className={styles.hexagon} data-aos="flip-left" data-aos-delay={delay} data-aos-duration="1000">    
                <p className={styles.icon}>{icon}</p>
            </div>
            <div data-aos="zoom-in" data-aos-delay={delay} className={styles.hexaDesc}>
                <h3>{title}</h3>
                <p>{desc}</p>               
            </div>
        </div>
    )
}

export default Hexa;