import React from 'react';
import styles from './Profile.module.css';
import {
    Bootstrap,
    CSS3,
    Heroku,
    Html,
    Js,
    MongoDB,
    NodeJs,
    Postgresql,
    ReactIcon,
    ReduxIcon,
    RestfulApi,
    Github
} from '../../../assets/images';

const Profile = () => {
    const iconsArr = [
        Html,
        CSS3,
        Js,
        Bootstrap,
        ReactIcon,
        ReduxIcon,
        RestfulApi,
        Github,
        Heroku,       
        MongoDB,
        NodeJs,
        Postgresql
    ];
    const iDescArr = [
        'HTML5',
        'CSS3',
        'Javascript',
        'Bootstrap',
        'React',
        'Redux',
        'RestfulApi',
        'Github',
        'Heroku',       
        'MongoDB',
        'NodeJs',
        'Postgresql'
    ];

    const skillsArr = iconsArr.map((skill, i) => {
        return (
            <li>
               <img className={styles.skillsIcon} src={skill} alt="skills-icon"/>
                {iDescArr[i]}
            </li>
        )
    })

    return (
        <div className={styles.profile}>                
                <div  data-aos="fade-right" data-aos-duration="500" data-aos-delay="1500">
                    <div className={styles.oct}>
                        <div>
                            <div></div>
                        </div>
                    </div>     
                    <p className={styles.aboutText}> 
                        I am a goal-oriented full stack web developer.
                        From being an Artist-Musician, 
                        I integrate my creativity and detail-oriented skills into web development.
                        I love the process of making ideas come to life
                        and develop products that improve the quality of user interaction.
                    </p>         
                </div>
                <div className={styles.aboutMe} data-aos="fade-left" data-aos-duration="500" data-aos-delay="1500">
                    <h3>Skills/Stack</h3>  
                    <ul className={styles.skills}>
                        {skillsArr}
                    </ul>              
                </div>
        </div>
    );
}

export default Profile;