import React,{Component} from 'react';
import styles from './Contact.module.css';
import Footer from '../../Components/Footer/Footer';

class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {
          name: '',
          email: '',
          message: '',
          sent: false,
          buttonText: 'Send Message'
        }
      }
      onNameChange(event) {
        this.setState({name: event.target.value})
      }
    
      onEmailChange(event) {
        this.setState({email: event.target.value})
      }
    
      onMessageChange(event) {
        this.setState({message: event.target.value})
      }
    
    handleSubmit(event) {
        event.preventDefault();

        this.setState({
            buttonText: '...sending'
        });

        fetch('http://localhost:3002/send',{
            method: "POST",
            body: JSON.stringify(this.state),
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
            },
        }).then(
            (response) => (response.json())
        ).then((response)=>{
        if (response.status === 'success'){
            this.setState({ sent: true },
            this.resetForm())
        }else if(response.status === 'fail'){
            alert("Message failed to send.")
        }
        })
    }
    resetForm(){
    
        this.setState({name: '', email: '', message: '', buttonText: 'Message Sent'})
     }
    render() {
        return (
            <div>
                <section className={styles.BG}>
                    <svg preserveAspectRatio="none" viewBox="0 0 100 102" height="75" width="100%" version="1.1" xmlns="http://www.w3.org/2000/svg" class="svgcolor-light">
                        <path d="M0 0 L50 100 L100 0 Z" fill="white" stroke="white"></path>
                    </svg>
                    <div className={styles.row}>
                        <h2 data-aos="fade-right">Contact</h2>
                        <div data-aos="fade-left" className={styles.headerBar}>&mdash;</div>
                    </div>
                    <form action="#" data-aos="zoom-in-up" data-aos-delay="700" id="contact-form" onSubmit={this.handleSubmit.bind(this)} method="POST">                               
                        <input type="text" name="name" placeholder = "Name" value={this.state.name} 
                            onChange={this.onNameChange.bind(this)} required /><br/>                      
                        <input type="email" name="email" placeholder = "Enter Email" value={this.state.email} 
                            onChange={this.onEmailChange.bind(this)} required /><br/>
                        <textarea placeholder = "Your Message" type = "text" name = "message" 
                            value={this.state.message} onChange={this.onMessageChange.bind(this)} ></textarea><br/>
                        <button type="submit">{this.state.buttonText}</button>
                    </form>
                </section>
                <Footer />
            </div>
        );
    }  
} 

export default Contact;