import React from 'react';
import styles from './ContactLinks.module.css';
import Tooltip from 'react-tooltip';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

const Contact = () => {
    return(
        <div className={styles.bodyColor}>
            <div className={styles.introContacts} data-aos="zoom-in-up" data-aos-duration="1000">
                <a href="https://www.linkedin.com/in/cecilia-nucaza-sy/"><div className={styles.icon} data-tip="linked-in"><FontAwesomeIcon icon={['fab', 'linkedin-in']}/></div></a>
                <a href= "https://github.com/cessycode"><div className={styles.icon} data-tip="github"><FontAwesomeIcon icon={['fab', 'github']}/></div></a>               
                <a href="https://codepen.io/cessycode"><div className={styles.icon} data-tip="codepen"><FontAwesomeIcon icon={['fab', 'codepen']}/></div></a>
                <a href="https://drive.google.com/file/d/1gX4hXLUstcG7jAvrETQpYa_x3s2JWVVN/view?usp=sharing"><div className={styles.icon} data-tip="download resume"><FontAwesomeIcon icon={['fas', 'file']}/></div></a>
            </div>    
            <Tooltip place="bottom"/>      
        </div>
    )
}

export default Contact;